	export default{
		data() {
			 const currentDate = this.getDate({
			            format: true
			        })
			return{
				type1: ['点击选择', '类型1', '类型2'],
				  type1Index: 0,
				  type2: ['点击选择', '类别1', '类别2'],
				    type2Index: 0,
					 date: currentDate,
					 locationAddress:"选择地点",
					 mark:""
			}
		},
		  computed: {
		        startDate() {
		            return this.getDate('start');
		        },
		        endDate() {
		            return this.getDate('end');
		        }
		    },
